.header {
  display: flex;
  margin-bottom: 24px;
  font-size: 17px;
  margin-left: 25px;

  .headerLink,
  .active{
    color: #fff;
    cursor: pointer;
    text-decoration: none;

    &:last-child {
      margin-left: 38px;
    }
  }

  .active {
    border-bottom: 1px solid #fff;
  }
}